<!-- 车辆轨迹 -->
<template>
	<el-container style="dispaly:flex;flex:1;">
		<!-- 右边地图 -->
		<el-container style="padding: 0;flex:1;display:flex;position:relative;flex-direction: column;">
			<el-aside style="position:relative;flex:1;width: 100%;">
				<div id="map"></div>
				<!-- 天气和工具 -->
				<div class="tool-bar">
					<a href="javascript:;" class="left" @click="downIsUpIconClick('city')">
						<img src="../../assets/image/carMonitor/toolIndex1.png"/>
						{{ curCity }}
						<span v-if="!cityDownIsUpShow" class="el-icon-arrow-down"></span>
						<span v-if="cityDownIsUpShow" class="el-icon-arrow-up"></span>
					</a>
					<span class="border"></span>
					<a href="javascript:;" class="right" @click="downIsUpIconClick('tool')">
						<img src="../../assets/image/carMonitor/toolIndex2.png"/>
						工具
						<span v-if="!toolDownIsUpShow" class="el-icon-arrow-down"></span>
						<span v-if="toolDownIsUpShow" class="el-icon-arrow-up"></span>
					</a>
				</div>
				<!-- 车辆详情弹窗 -->
				<div v-if="carPopupHide" class="car-info">
					<div class="title">
						{{ carInfo.carNum }}
						<span @click="clickPopupHide" class="hide el-icon-close"></span>
					</div>
					<div class="content">
						<div class="li-bar">
							<span class="name">速度：</span>
							<em class="text">{{ carInfo.spped }}</em>
						</div>
						<div class="li-bar">
							<span class="name">本月行驶里程：</span>
							<em class="text">{{ carInfo.mileage }}</em>
						</div>
						<div class="li-bar">
							<span class="name">定位时间：</span>
							<em class="text">{{ carInfo.locationDate }}</em>
						</div>
						<div class="li-bar">
							<span class="name">定位地址：</span>
							<em class="text">{{ carInfo.locationAddress }}</em>
						</div>
						<div class="li-bar">
							<span class="name">司机姓名：</span>
							<em class="text">{{ carInfo.driverName }}</em>
						</div>
						<div class="li-bar">
							<span class="name">轮胎情况：</span>
							<em class="text color-red">{{ carInfo.tyreInfo }}</em>
							<a href="javascript:;" class="more">点击查看详情</a>
						</div>
						<div class="btn-bar">
							<router-link to="/home/carMonitor/allMonitor">实时监控</router-link>
						</div>
					</div>
				</div>
				<!-- 地图右侧驾驶行为 -->
				<div class="right-popup">
					<div class="lengthKm-bar">
						<div class="length-km">
							<el-tooltip effect="dark" content="行驶总里程(km)" placement="bottom">
								<span class="tip"></span>
							</el-tooltip>
							<img src="../../assets/image/map/lengthKmIndex1.png"/>
							<span class="num">{{ lengthKm }}</span>
						</div>
						<div class="length-time">
							<el-tooltip effect="dark" content="驾驶总时长(min)" placement="bottom">
								<span class="tip"></span>
							</el-tooltip>
							<img src="../../assets/image/map/lengthKmIndex2.png"/>
							<span class="num">{{ lengthTime }}</span>
						</div>
					</div>
					<div class="more-bar">
						<a href="javascript:;" class="el-icon-close hide"></a>
						<div class="radio">
							<span>驾驶行为分析</span>
					<!-- <el-switch style="float:left;" v-model="radio" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
						<a href="javascript:;" class="more">更多</a> -->
					</div>
					<ul class="type">
						<li>
							<span :class="{ 'color-red' : checked1 }" class="num">20</span>
							<el-checkbox v-model="checked1">超速</el-checkbox>
						</li>
						<li>
							<span :class="{ 'color-red' : checked2 }" class="num">3</span>
							<el-checkbox v-model="checked2">停留</el-checkbox>
						</li>
						<li>
							<span :class="{ 'color-red' : checked3 }" class="num">0</span>
							<el-checkbox v-model="checked3">急加速</el-checkbox>
						</li>
						<li>
							<span :class="{ 'color-red' : checked4 }" class="num">0</span>
							<el-checkbox v-model="checked4">急减速</el-checkbox>
						</li>
					</ul>
				</div>
			</div>
			<!-- 轨迹回放控制器 -->
			<!-- <div class="controller">
				<em v-if="!contrSwitch" @click="clickStratPlay" class="icon iconfont">&#xe61c;</em>
				<em v-if="contrSwitch" @click="clickEndPlay" class="icon iconfont">&#xe600;</em>
				<em class="icon iconfont" @click="clickRestartPlay">&#xe67b;</em>
				<div class="block">
					<el-progress :text-inside="true" style="width:110px;margin: 6px 6px 0 0;float:left;" :show-text="false" :stroke-width="2" :percentage="progress"></el-progress>
					<span>{{ progress }}%</span>
				</div>
				<div class="block slider">
					<span class="demonstration">快</span>
					<el-slider @change="changeSlider" :show-tooltip="false" style="width:110px;margin: 0 10px;float:left;"  v-model="slider"></el-slider>
					<span class="demonstration">慢</span>
				</div>
			</div> -->
			<!-- 轨迹回放控制器2 -->
			<div class="controller">
				<div class="controller-btn">
					<div class="input" @click.stop @contextmenu.stop>
						<el-input v-model="carNumValue" class="input" placeholder="请选择车牌号">
							<i slot="suffix" @click="clickTreeShow" class="el-input__icon down-icon el-icon-caret-bottom"></i>
						</el-input>
					</div>
					<el-date-picker v-model="selectDate" style="width: 360px;float:left;margin-right:5px;" type="datetimerange" :picker-options="pickerOptions" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" align="right"></el-date-picker>
					<em class="icon iconfont">&#xe726;</em>
					<el-tooltip v-if="!contrSwitch" class="item" effect="dark" content="播放轨迹" placement="top">
						<em @click="clickStratPlay" class="icon iconfont">&#xe61c;</em>
					</el-tooltip>
					<em v-if="contrSwitch" @click="clickEndPlay" class="icon iconfont">&#xe600;</em>
					<em class="icon iconfont">&#xe614;</em>
				</div>
				<div class="block slider">
					<el-slider :max="max" @change="changeSlider" :show-tooltip="false" style="width:100%;"  v-model="progress"></el-slider>
				</div>
				<!-- 选择车辆的树 -->
				<div class="car-tree" @click.stop @contextmenu.stop v-if="$store.state.treeShow">
					<div class="search-bar">
						<el-input size="small" v-model="searchValue" class="input"></el-input>
						<el-button  type="primary" size="small" icon="el-icon-search">搜索</el-button>
					</div>
					<el-aside width="100%" style="margin: 10px 0 0;flex:1;">
						<el-tree empty-text default-expand-all :expand-on-click-node="expandNode" @check="changeChecked" :data="sideTree" show-checkbox node-key="id" @node-click="handleNodeSideClick" highlight-current :default-expanded-keys="openList" ref="sideTree" :props="defaultProps">
						</el-tree>
					</el-aside>
				</div>
			</div>	
		</el-aside>
		<!-- <ul class="tab-btn">
			<li @click="clickFootTab(index)" :class="{ 'active': footTabCurIndex == index }" v-for="(v,index) in footTabBtnList">{{ v.text }}</li>
		</ul> -->
		<el-aside style="padding: 10px 0 0;text-align:center;" class="center" v-if="footTabCurIndex != null" width="100%">
			<el-table max-height="140" ref="singleTable" size="mini" highlight-current-row :data="tableData" border>
				<template v-for="v in tableTopList">
					<el-table-column :prop="v.english" width="300" :label="v.text"></el-table-column>
				</template>
			</el-table>
		</el-aside>
	</el-container>
</el-container>
</template>

<script>
import store from '@/store'
var map,pointArr = [],tid;
export default {
	name: 'carAnalysis',
	data() {
		return {
			searchInput: '',
			expandNode: false,
			cityDownIsUpShow: false,
			toolDownIsUpShow: false,
			curCity: '呼和浩特',
			carPopupHide: false,
			selectDate: '',
			celectDateIcon: false,
			footTabCurIndex: null,
			lengthKm: '88999',
			lengthTime: '780',
			radio: true,
			checked1: false,
			checked2: false,
			checked3: false,
			checked4: false,
			markerCurIndex: 0,
			lock: true,
			contrSwitch: false,
			progress: 0,
			max: 100,
			slider: 0,
			endApply: false,
			timeNum: 100,
			treeShow: false,
			searchValue: '',
			carNumValue: '',
			tableTopList:[
			{
				english: 'startTime',
				text: '停车开始时间'
			},
			{
				english: 'endTime',
				text: '停车结束时间'
			},
			{
				english: 'TimeLength',
				text: '停车时长'
			},
			{
				english: 'lengthKm',
				text: '里程(km)'
			},
			{
				english: 'travlLengKm',
				text: '行驶里程(km)'
			}

			],
			footTabBtnList: [
			{
				text: '轨迹点'
			},
			{
				text: '停车点'
			},
			{
				text: '超速'
			},
			{
				text: '急变速'
			},
			{
				text: '急转弯'
			}
			],
			tableData: [
			{
				id:'1',
				startTime: '2018-02-12',
				endTime:'2018-02-20',
				TimeLength: '20',
				lengthKm: '50',
				travlLengKm: '80'
			},
			{
				id:'2',
				startTime: '2018-02-12',
				endTime:'2018-02-20',
				TimeLength: '20',
				lengthKm: '50',
				travlLengKm: '80'
			},
			{
				id:'3',
				startTime: '2018-02-12',
				endTime:'2018-02-20',
				TimeLength: '20',
				lengthKm: '50',
				travlLengKm: '80'
			},
			{
				id:'4',
				startTime: '2018-02-12',
				endTime:'2018-02-20',
				TimeLength: '20',
				lengthKm: '50',
				travlLengKm: '80'
			}
			],
			pickerOptions: {
				shortcuts: [{
					text: '昨天',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24);
						picker.$emit('pick', [start, end]);
					}
				},{
					text: '前天',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 2);
						picker.$emit('pick', [start, end]);
					}
				},{
					text: '最近一周',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
						picker.$emit('pick', [start, end]);
					}
				}, {
					text: '最近一个月',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
						picker.$emit('pick', [start, end]);
					}
				}, {
					text: '最近三个月',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
						picker.$emit('pick', [start, end]);
					}
				}]
			},
			carInfo: {
				carNum: '粤B23456',
				spped: '5km/s',
				mileage: '8999km',
				locationDate: '2018-07-11 17:00:00',
				locationAddress: '广东省深圳市南山区',
				driverName: '老张',
				tyreInfo: '轮胎异常，胎压异常'
			},
			sideTree: [{
				id:'1',
				text: '深圳赤子物流有限公司',
				children: [{
					id:'2',
					text: '操作部',
					children: [{
						id:'7',
						text: '粤C24523'
					},
					{
						id:'9',
						text: '粤C24873'
					}]
				},
				{
					id:'3',
					text: '新车',
					children: [{
						id:'10',
						text: '粤C24523'
					},
					{
						id:'11',
						text: '粤C24873'
					}]
				}]
			}],
			openList: [],
			defaultProps: {
				children: 'children',
				label: 'text'
			},
		}
	},
	created() {

	},
	mounted() {
		// this.qeuryLocation();
		/*var driving = new BMap.DrivingRoute("北京", {
			renderOptions: {
				map: map
			}
		});
		driving.search({point: new BMap.Point(104.047789,30.648128)}, {point: new BMap.Point(104.164008,30.71531)});*/
		map = new BMap.Map('map', {enableMapClick:false});
		var point = new BMap.Point(108.840053, 34.129522);
		map.centerAndZoom(point, 15);
		map.enableScrollWheelZoom();
		map.addControl(new BMap.ScaleControl());
		var pointStr = "104.047789,30.648128,104.063959,30.655336,104.067264,30.660307,104.067264,30.664438,104.064008,30.665316,104.074008,30.665316,104.084008,30.665316,104.094008,30.665316,104.104008,30.665316,104.114008,30.665316,104.124008,30.66531,104.134008,30.67531,104.144008,30.68531,104.154008,30.69531,104.164008,30.70531,104.164008,30.71531".split(",");
		pointArr = [];
		for (var k = 0; k < pointStr.length; k += 2) {
			pointArr.push({
				lng: pointStr[k],
				lat: pointStr[k + 1]
			});
		}
		this.max = pointArr.length;	
	},
	methods: {
		loadBMapScript () {
			let script = document.createElement('script');
			script.id = 'script';
			script.src = 'http://api.map.baidu.com/api?v=2.0&ak=dF8sMa2zZUk8znVfxyvzxQjsiQWrDfW2&callback=bMapInit';
			document.body.appendChild(script);
			console.log(document.body)
		},
		downIsUpIconClick(text){
			// var list = map.getOverlays();
			// for(var i in list) {
			// 	if(i % 2) {
			// 		map.removeOverlay(list[i]);
			// 	}
			// }
			if(text == 'city') {
				this.cityDownIsUpShow = !this.cityDownIsUpShow;
			} else {
				this.toolDownIsUpShow = !this.toolDownIsUpShow;
			}
		},
		setMarkerIcon(map,pointArr) {
			var leng = pointArr.length;
			var num = this.markerCurIndex;
			var progress = this.progress;
			if(leng > num) {
				// var list = map.getOverlays(),index = list.length;
				// if(index - leng > 0) {//可以加判断如果是停车的话不清除上一个图标
				// 	map.removeOverlay(list[index - 1]);
				// }
				this.markerCurIndex++;
				if(num == this.progress){
					this.progress = this.markerCurIndex;
				}
				if(num == 0) {
					var img = require('../../assets/image/map/shi.png');
					var myIcon = new BMap.Icon(img, new BMap.Size(28,34));
					var offset = new BMap.Size(0, -20);
					// var myStart = new BMap.Marker(start,{icon:startIcon});
					// map.addOverlay(myStart); 
				} else if(num == leng - 1) {
					var img = require('../../assets/image/map/zhong.png');
					var myIcon = new BMap.Icon(img, new BMap.Size(28,34));
					var offset = new BMap.Size(0, -20);
				}
				var trackPoint = [];
				var lineColor = '#00b7ee';
				trackPoint = [];
				trackPoint.push(new BMap.Point(pointArr[num].lng, pointArr[num].lat));
				if(num + 1 > leng -1) {
					trackPoint.push(new BMap.Point(pointArr[num].lng, pointArr[num].lat));
				} else {
					trackPoint.push(new BMap.Point(pointArr[num+1].lng, pointArr[num+1].lat));
				}
				var polyline = new BMap.Polyline(trackPoint, {
					strokeColor: lineColor,
					strokeWeight: 5,
					strokeOpacity: 1
				});
				polyline.id = '2';
				map.addOverlay(polyline);
				var pt = new BMap.Point(pointArr[num].lng,pointArr[num].lat);
				if(num == 0){
					var marker = new BMap.Marker(pt,{
						icon:myIcon,
						offset: offset
	       			});  // 创建标注
					marker.id = '0';
				} else if(num == leng - 1) {
					var marker = new BMap.Marker(pt,{
						icon:myIcon,
						offset: offset
	       			});  // 创建标注
					marker.id = '1';
				}
				map.addOverlay(marker); // 将标注添加到地图中
				// marker.setRotation(90);//旋转角度
				var zoom = map.getZoom();
				map.centerAndZoom(pt,zoom);
				if(this.contrSwitch) {
					if(progress > num) {
						this.setMarkerIcon(map,pointArr);
					} else if(progress < num) {
						var list = map.getOverlays();
						this.markerCurIndex = this.progress;
						for(var i = list.length - 1;i >= num - progress;i--) {
							map.removeOverlay(list[i]);
							if(list[i].id != '2') {
								i++;
							}
						}
						this.setMarkerIcon(map,pointArr);
					} else {
						tid = setTimeout(()=>{
							this.setMarkerIcon(map,pointArr);
						},this.timeNum);
					}	
				}
			} else {
				this.contrSwitch = false;
				this.lock = true;
			}
		},
		qeuryLocation() {
			map = new BMap.Map('map', {enableMapClick:false});
			var point = new BMap.Point(108.840053, 34.129522);
			map.centerAndZoom(point, 15);
			map.enableScrollWheelZoom();
			map.addControl(new BMap.ScaleControl());
			var pointStr = "104.047789,30.648128,104.063959,30.655336,104.067264,30.660307,104.067264,30.664438,104.064008,30.665316,104.074008,30.665316,104.084008,30.665316,104.094008,30.665316,104.104008,30.665316,104.114008,30.665316,104.124008,30.66531,104.134008,30.67531,104.144008,30.68531,104.154008,30.69531,104.164008,30.70531,104.164008,30.71531".split(",");
			pointArr = [];

			for (var k = 0; k < pointStr.length; k += 2) {
				pointArr.push({
					lng: pointStr[k],
					lat: pointStr[k + 1]
				});
			}
			// 生成坐标点
			var trackPoint = [];
			var lineColor = '#00b7ee';
			for(var i = 0;i < pointArr.length-1;i++) {
				trackPoint = [];
				trackPoint.push(new BMap.Point(pointArr[i].lng, pointArr[i].lat));
				trackPoint.push(new BMap.Point(pointArr[i+1].lng, pointArr[i+1].lat));
	   		    //区间颜色
	   		    // if(i == 1){
	   		    // 	lineColor="yellow";
	   		    // }else if(i == 2){
	   		    // 	lineColor="green";
	   		    // }else if(i == 3){
	   		    // 	lineColor="black";
	   		    // }else if(i == 4) {
	   		    // 	lineColor="red";
	   		    // }
				// 画线
				var polyline = new BMap.Polyline(trackPoint, {
					strokeColor: lineColor,
					strokeWeight: 5,
					// strokeStyle:"dashed",
					strokeOpacity: 1
				});
				map.addOverlay(polyline);
			}
	   		// 配置图片
	   		/*var size = new BMap.Size(18, 18);
	   		var offset = new BMap.Size(0, 0);
	   		var imageSize = new BMap.Size(18, 18);
	   		var icon = new BMap.Icon(require("../../assets/image/map/mapTrailIcon1.png"), size, {
	   			imageSize: imageSize,
	   			infoWindowAnchor:new BMap.Size(0, -3)
	   		});
	   		// 画图标
	   		for (var i = 0;i < pointArr.length;i++) {
	   			var track = new BMap.Point(pointArr[i].lng, pointArr[i].lat)
	   			var marker = new BMap.Marker(track, {
	   				icon: icon,
	   				offset: offset
	   			});
	   			// 创建标注
	   			map.addOverlay(marker);
	   			marker.addEventListener("click",function () {
	   				var point=this.getPosition();
	   			})
	   		}*/
	   		this.setZoom(pointArr,map);
	   	},
	   	//根据经纬极值计算绽放级别。 (从网上复制)
	   	getZoom(maxLng, minLng, maxLat, minLat,map) {
	        var zoom = ["50", "100", "200", "500", "1000", "2000", "5000", "10000", "20000", "25000", "50000", "100000", "200000", "500000", "1000000", "2000000"]; // 级别18到3。
	        var pointA = new BMap.Point(maxLng, maxLat); // 创建点坐标A
	        var pointB = new BMap.Point(minLng, minLat); // 创建点坐标B
	        var distance = map.getDistance(pointA, pointB).toFixed(1); //获取两点距离,保留小数点后两位
	        for (var i = 0, zoomLen = zoom.length; i < zoomLen; i++) {
	        	if (zoom[i] - distance > 0) {
	                return 18 - i + 3; //之所以会多3，是因为地图范围常常是比例尺距离的10倍以上。所以级别会增加3。
	            }
	        }
	    },
	    //设置地图缩放
	    setZoom(points,map) {
	    	if (points.length > 0) {
	    		var maxLng = points[0].lng;
	    		var minLng = points[0].lng;
	    		var maxLat = points[0].lat;
	    		var minLat = points[0].lat;
	    		var res;
	    		for (var i = points.length - 1; i >= 0; i--) {
	    			res = points[i];
	    			if (res.lng > maxLng) maxLng = res.lng;
	    			if (res.lng < minLng) minLng = res.lng;
	    			if (res.lat > maxLat) maxLat = res.lat;
	    			if (res.lat < minLat) minLat = res.lat;
	    		}
	    		var cenLng = (parseFloat(maxLng) + parseFloat(minLng)) / 2;
	    		var cenLat = (parseFloat(maxLat) + parseFloat(minLat)) / 2;
	    		var zoom = this.getZoom(maxLng, minLng, maxLat, minLat,map);
	    		map.centerAndZoom(new BMap.Point(cenLng, cenLat), zoom ? zoom : 5);
	    	} else {
	            //没有坐标，显示全中国
	            map.centerAndZoom(new BMap.Point(103.388611, 35.563611), 12);
	        }
	    },
	    changeChecked() {

	    },
	    handleNodeSideClick(data) {
	    	this.carNumValue = data.text;
	    	this.$store.state.treeShow = false;
	    },
	    clickPopupShow() {
	    	this.carPopupHide = true;
	    },
	    clickPopupHide() {
	    	this.carPopupHide = false;
	    },
	    clickFootTab(index) {
	    	this.footTabCurIndex = index;
	    },
	    //轨迹开始播放
	    clickStratPlay(){
	    	if(!this.lock) {
	    		return
	    	}
	    	this.contrSwitch = true;
	    	if(pointArr.length == this.markerCurIndex) {
	    		this.markerCurIndex = 0;
	    		this.progress = 0;
	    		clearTimeout(tid);
	    		var list = map.getOverlays();
	    		for(var i in list) {
	    			map.removeOverlay(list[i]);
	    		}
	    		// this.qeuryLocation();
	    		this.setMarkerIcon(map,pointArr);
	    	} else {
	    		this.setMarkerIcon(map,pointArr);
	    	}
	    },
	    //轨迹暂停播放
	    clickEndPlay(){
	    	clearTimeout(tid);
	    	this.lock = true;
	    	this.contrSwitch = false;
	    },
	    //轨迹重新播放
	    clickRestartPlay() {
	    	this.lock = true;
	    	this.markerCurIndex = 0;
	    	this.percentage = 0;
	    	this.contrSwitch = true;
	    	clearTimeout(tid);
	    	var list = map.getOverlays();
	    	for(var i in list) {
	    		map.removeOverlay(list[i]);
	    	}
	    	// this.qeuryLocation();
	    	this.setMarkerIcon(map,pointArr);
	    },
	    //拖动滑块
	    changeSlider(e) {
	    	this.clickStratPlay();
	    },
	    //点击显示树
	    clickTreeShow(){
	    	this.$store.state.treeShow = true;
	    }
	},
	store
}
</script>

<style scoped>
#map{width: 100%;padding: 0;height: 100%;}
.side-search{margin: 0 0 10px;}
.side-search .search-input{width: 100%;padding: 0 45px 0 10px;border: 1px solid #d2d2d2;height: 32px;color: #666;border-radius: 3px;box-sizing: border-box;}
.side-search .el-icon-search{cursor: pointer;}
.side-box{padding: 10px 0 0 10px;width:280px;display:flex;flex-direction:column;}
.btn{width: 200px;height: 48px;line-height: 48px;text-align: center;background: #303c4d;color: #fff;font-size: 14px;margin: 30px auto 0;display: block;border: none;border-radius: 4px;}
.tool-bar{position: absolute;top: 10px;left: 10px;overflow: hidden;z-index: 19;background: #303c4d;border-radius: 4px;padding: 5px;}
.tool-bar a{float: left;height: 21px;line-height: 21px;font-size: 12px;color: #fff;display: block;}
.tool-bar a img{float: left;width: 26px;height: 21px;margin-right: 5px;}
.tool-bar .border{display: block;width: 1px;height: 21px;background: #fff;margin: 0 10px;float: left;}
.car-info{width: 380px;padding: 0 0 10px;z-index: 18;position: absolute;top: 50%;left: 50%;margin-left: -185px;margin-top: -133px;}
.car-info .title{font-size: 16px;color: #fff;background: #303c4d;height: 40px;line-height: 40px;position: relative;padding: 0 20px;border-radius: 4px 4px 0 0;}
.hide{float: right;font-size: 20px;display: block;line-height: 40px;cursor: pointer;}
.content{background: #fff;padding: 0 20px 10px;border-radius: 0 0 4px 4px;}
.li-bar{overflow: hidden;line-height: 30px;font-size: 14px;}
.li-bar span{float: left;color: #444;display: block;}
.li-bar em{float: left;color: #999;display: block;}
.li-bar .color-red{color: #fc0101;}
.li-bar .more{float: right;color: #4dc4f5;display: block;text-decoration: underline;}
.btn-bar{overflow: hidden;padding: 10px 0 0;}
.btn-bar a{display: block;padding: 0 10px;height: 26px;line-height: 26px;background: #303c4d;column-rule-color: #fff;float: left;margin-right: 10px;}
.tab-btn{overflow: hidden;border-bottom: 2px solid #0ba5dc;}
.tab-btn li{float: left;cursor: pointer;border: 1px solid #cfcfcf;border-bottom: none;color: #666;font-size: 14px;width: 66px;height: 32px;line-height: 32px;text-align: center;box-sizing: border-box;}
.tab-btn .active{color: #fff;background: #0ba5dc;border: none;}
.right-popup{position: absolute;top: 10px;right: 10px;z-index: 19;overflow: hidden;width: 330px;}
.right-popup .lengthKm-bar{overflow: hidden;}
.right-popup .length-km,.right-popup .length-time{float: left;width: 100px;border-radius: 4px;color: #fff;text-align: center;padding: 5px 0 0;position: relative;}
.right-popup .length-km .tip,.right-popup .length-time .tip{position: absolute;top: 0;left: 0;width: 100%;height: 100%;}
.right-popup .length-km{background: #eb6877;}
.right-popup .length-time{background: #18baf4;float: right;}
.right-popup .length-km img,.right-popup .length-time img{display: block;width: 28px;height: 23px;margin: 0 auto;}
.right-popup .length-km .num,.right-popup .length-time .num{font-size: 16px;line-height: 26px;}
.right-popup .more-bar{width: 100%;border: 1px solid #666;border-radius: 4px;padding: 10px 10px 0;box-sizing: border-box;background: #fff;margin: 10px 0 0;position: relative;}
.right-popup .radio{overflow: hidden;}
.right-popup .radio span{display: block;float: left;line-height: 20px;margin-right: 10px;color: #666;}
.right-popup .radio a{display: block;float: right;line-height: 20px;margin-right: 10px;color: #18baf4;margin-right: 30px;text-decoration: underline;}
.right-popup .type{padding: 20px 0 0;display: flex;}
.right-popup .type li{flex: 1;border-right: 1px solid #e5e5e5;padding: 0 0 10px;display: flex;justify-content: center;flex-direction: column;align-items: center;}
.right-popup .type li:last-child{border: none;}
.right-popup li .num{display: block;color: #999;line-height: 30px;font-size: 22px;padding-bottom: 16px;text-align: center;}
.right-popup li .color-red{color: #fc0101;}
.more-bar .hide{position: absolute;display: block;font-size: 20px;top: 3px;right: 5px;color: #c5c5c5;line-height: 24px;}
/*.controller{position: absolute;left: 50%;bottom: 60px;margin-left: -200px;overflow: hidden;padding: 0 5px;background: #fff;height: 36px;z-index: 20;width: 394px;}*/
.controller{position: absolute;left: 10px;bottom: 10px;padding: 10px 0 0;background: #fff;z-index: 20;width: 690px;box-shadow: 1px 5px 5px 0 rgba(0,0,0,.4);box-sizing: border-box;}
.controller .input{width: 160px;float: left;margin-right: 10px;}
.controller .down-icon{font-size: 20px;cursor: pointer;}
.controller .block{width: 100%;}
.controller-btn{overflow: hidden;padding: 0 10px;}
/*.controller em{display: block;float: left;margin: 0 5px;cursor: pointer;font-size: 20px;line-height: 36px;}*/
.controller em{display: block;float: left;margin: 2px 4px 0;cursor: pointer;font-size: 20px;width: 36px;height: 36px;line-height: 36px;text-align: center;color: #fff;background: #00a8d4;border-radius: 50%;}
.controller .block{margin-top: 11px;padding: 0 10px;box-sizing: border-box;}

.car-tree{position: absolute;bottom: 85px;left: 10px;width: 280px;height: 300px;background: #fff;display: flex;flex-direction: column;}
.car-tree .search-bar{padding: 6px 0 0 6px;}
</style>



